<template>
	<div style="background-color: white; min-height: 100%;">
    	<myHeader :title="''"></myHeader>

    	<div>
    		<div class="logo">
    			 <img src="static/image/logo.jpg" />
        <center><span style="color:#3F51B5;margin-top:10px;font-size:17px"></span></center>
    		</div>
    		<div class="iptBox aui-list aui-form-list">
    			<div class="aui-list-item">
    				<div class="aui-list-item-inner">
    					<div class="aui-list-item-label-icon">
    						<img src="../../../static/image/mobileatByFoot.png" alt="" width="18" />
    					</div>
    					<div class="aui-list-item-input" style="padding-right:0;">
    						<input type="tel" v-model="mobile" placeholder="">
    					</div>
    					<div class="aui-font-size-12" style="color:#f22a2a;padding-right:0;width:160px;text-align:center;border-left:1px solid #fafafa;" v-if="isshowCount"><span>{{time}}</span>s</div>
    					<div class="aui-font-size-12" style="color:#f22a2a;padding-right:0;width:160px;text-align:center;border-left:1px solid #fafafa;" v-else @click="getCodeBefore">{{codeMsg}}</div>
    				</div>
    			</div>
    			<div class="aui-list-item">
    				<div class="aui-list-item-inner">
    					<div class="aui-list-item-input" style="padding-right:0;">
    						<input type="tel" v-model="code" placeholder="">
    					</div>
    				</div>
    			</div>
    			<div class="aui-list-item">
    				<div class="aui-list-item-inner">
    					<div class="aui-list-item-label-icon">
    						<img src="../../../static/image/pswatByFoot.png" alt="" width="18" />
    					</div>
    					<div class="aui-list-item-input">
    						<input type="password" v-model="password" placeholder="">
    					</div>
    				</div>
    			</div>
    			<div class="aui-btn aui-btn-danger aui-btn-block" @click="register_btn1"> </div>
    			<div class="aui-font-size-14 aui-margin-10" style="text-align:center;"><a style="color:#f22a2a;" @click="openRegisterProtocol"></a></div>
    		</div>
    	</div>
    </div>
</template>

<script>
	import md5 from '../public/md5'
	
	export default {
		name: 'register',
		data() {
			return {
				mobile:'',//
				code:'',//
				password:'',//
				isshowCount:false,//
				codeMsg:'',//
				time:60//
			}
		},
		methods: {
			//
			register_btn1(){
               this.$router.pushRoute({name:"xiayibu"});
			},
			openRegisterProtocol() {
				this.$router.pushRoute({name:"registerProtocol"});
			},
			//
			countDown() {
				var that = this;
				var timer = setInterval(function(){
					if(that.time<=0){
						that.codeMsg = "";
						that.isshowCount = false;
						that.time = 60;
						clearInterval(timer);
						return
					}
					that.time--;
				},1000)
			},
			//
			getCodeBefore() {
				var that = this;

				//
				var url1 = 'f_user/count?filter={"where":{"User_name":' + that.mobile + '}}';
				that.ajax({url:url1,method:'GET',
					success:function(data){
						if(data.count!==0) {
							that.$MessageBox.alert('');
							return
						}else {
							that.getCode();
						}
					}
				})
			},
			//
			getCode() {
				var that = this;
				if(!/^1[34578]\d{9}$/.test(that.mobile)) {
					this.$MessageBox.alert('');
					return
				}
				that.isshowCount = true;
				that.countDown();
				var sign = "jsjy" + that.mobile + "cgyc";
				sign = md5(sign);
				sign = sign.toUpperCase();
				var url = 'http://101.200.169.185/getTtSMSCode?mobile=' + that.mobile + '&sign=' + sign;
				that.$http.get(url).then(function(response){
					if(response.data.code=="200"){
						that.checkIfCode(response.data.smsCode);
					}
				}).catch(function(error){

				})
			},
			//
			checkIfCode(code) {
				var that = this;
				var url = 'code?filter={"where":{"mobile":'+ that.mobile +'}}';
				var method = "GET";
				that.ajax({url,method,
					success:function(response){
						if(response.length==0) {
							that.storeCode(code);
						}else {
							that.updateCode(code,response[0].id)
						}
					},
					error:function(data){}
				})
			},
			//
			storeCode(code) {
				var that = this;
				var url = "code";
				var method = "POST";
				var params = {
					"data":{
						"mobile":that.mobile,
						"status":"0",
						"code":code
					}
				}
				that.ajax({url,method,params,
					success:function(response){
					},
					error:function(data){}
				})
			},
			//code
			updateCode(code,id) {
				var that = this;
				var url = "code/" + id;
				var method = "POST";
				var params = {
					"data":{
						"code":code,
						"_method":"PUT"
					}
				}
				that.ajax({url,method,params,
					success:function(response){
					},
					error:function(data){}
				})
			},
			register_btn() {
				if(!/^1[34578]\d{9}$/.test(this.mobile)) {
					this.$MessageBox.alert('');
					return
				}
				if(!this.code) {
					this.$MessageBox.alert('');
					return
				}
				if(!this.password) {
					this.$MessageBox.alert('');
					return
				}
				this.registerAjax();
			},
			registerAjax() {
				var that = this;
				that.$Indicator.open();
				//
				var url = 'code?filter={"where":{"mobile":'+ that.mobile +'}}';
				var method = "GET";
				that.ajax({url,method,
					success:function(response){
						if(response.length>0){
							var code = response[0].code;
							var time = new Date(response[0].updatedAt);
							time = time.getTime();
							var now = new Date().getTime();
							var shix = now-time;
							if(code==that.code&&shix<600000){
								//
								var url2 = "f_user?filter[fields][password]=false";
								var params = {
									"data": {
										"User_name": that.mobile,
										"password": that.password,
										"Name": that.mobile,
										"Tx": "../../../static/image/xitong@3x.png",
										"info": 1
									}
								}
								that.ajax({url:url2,method:'POST',params,
									success:function(data){
										if(data){
											that.$Indicator.close();
											window.localStorage.setItem('userMobile',data.User_name);
											window.localStorage.setItem('userId',data.id);
											that.getSubscribe(data.id);
										}
									}
								})
							} else {
								that.$Indicator.close();
								that.$MessageBox.alert("");
							}
						} else {
							that.$Indicator.close();
							that.$MessageBox.alert("");
						}
					},
					error:function(data){}
				})
			},
			//
			getSubscribe(id) {
				var that = this;
				var filter = "filter[where][status]=0";
				filter += "&filter[where][up]=0";
				filter += "&filter[where][to]=index";
				filter += "&filter[skip]=0";
				filter += "&filter[limit]=5000";
				var url = "news_info?" + filter;
				var method = "GET";
				that.ajax({url,method,
					success:function(response){
						if(response.length>0){
							that.addSubscribe(id,response);
						}
					},
					error:function(error){
					}
				})
			},
			//
			addSubscribe(id, arr) {
				var that = this;
				var requests = [];
				for(var i = 0;i<arr.length;i++){
					var json = {};
					json.path = "/mcm/api/user_dingyue";
					json.method = "POST";
					var body = {};
					body.user_id = id;
					body.info = arr[i].id;
					json.body = body;
					requests.push(json);
				}
				var url = "batch/";
				var method = "POST";
				var params = {
					"data":{
						"requests":requests
					}
				}
				that.ajax({url,method,params,
					success:function(response){
						that.$Indicator.close();
						that.$router.backRoute();
					}
				})
			}
		},
		activated() {
			this.mobile = '';
			this.code = '';
			this.password = '';
		}
	}
</script>

<style scoped>
	.logo {
		width: 4rem;
		margin: 1.8rem auto 0;
	}
	.iptBox {
		margin:2.25rem auto 0;
		width:17.25rem;
	}
	.aui-btn-danger {
		background-color: #27B5B1 !important;
		margin-top: 1.5rem;
	}
	.aui-list .aui-list-item-inner {
		margin-right: 0;
	}
</style>



