<template>
	<div style="background-color: white; min-height: 100%;">
    	<myHeader :title="''"></myHeader>

    	<div>
    		<div class="iptBox aui-list aui-form-list">
    			<div class="aui-list-item">
    				<div class="aui-list-item-inner">
    					<div class="aui-list-item-label-icon">
    						<img src="../../../static/image/mobileatByFoot.png" alt="" width="18" />
    					</div>
    					<div class="aui-list-item-input" style="padding-right:0;">
    						<input type="tel" v-model="mobile" placeholder="">
    					</div>
    					<div class="aui-font-size-12" style="color:#f22a2a;padding-right:0;width:160px;text-align:center;border-left:1px solid #fafafa;" v-if="isshowCount"><span>{{time}}</span>s</div>
    					<div class="aui-font-size-12" style="color:#f22a2a;padding-right:0;width:160px;text-align:center;border-left:1px solid #fafafa;" v-else @click="getCodeBefore(1)">{{codeMsg}}</div>
    				</div>
    			</div>
    			<div class="aui-list-item">
    				<div class="aui-list-item-inner">
    					<div class="aui-list-item-input" style="padding-right:0;">
    						<input type="tel" v-model="code" placeholder="">
    					</div>
    				</div>
    			</div>
    			<div class="aui-list-item">
    				<div class="aui-list-item-inner">
    					<div class="aui-list-item-label-icon">
    						<img src="../../../static/image/pswatByFoot.png" alt="" width="18" />
    					</div>
    					<div class="aui-list-item-input">
    						<input type="password" placeholder="" v-model="password">
    					</div>
    				</div>
    			</div>
    			<div class="aui-btn  aui-btn-block" @click="getCodeBefore(2)"></div>
    		</div>
    	</div>
    </div>
</template>

<script>
	import md5 from '../public/md5'
	
	export default {
		name: 'forgetPsw',
		data() {
			return {
				userId:"",
				mobile:'',//
				code:'',//
				password:'',//
				isshowCount:false,//
				codeMsg:'',//
				time:60//
			}
		},
		methods: {
			//
			countDown() {
				var that = this;
				var timer = setInterval(function(){
					if(that.time<=0){
						that.codeMsg = "";
						that.isshowCount = false;
						that.time = 60;
						clearInterval(timer);
						return
					}
					that.time--;
				},1000)
			},
			//
			getCodeBefore(type) {
				var that = this;

				if(!/^1[34578]\d{9}$/.test(that.mobile)) {
					this.$MessageBox.alert('');
					return
				}
				//
				var url1 = 'f_user?filter={"where":{"User_name":' + that.mobile + '}}';
				that.ajax({url:url1,method:'GET',
					success:function(data){
						if (JSON.stringify(data)=="[]") {
							that.$MessageBox.alert('');
							return
						} else {
							that.userId = data[0].id;
							if (type=="1") {
								that.getCode();
							} else {
								that.fgtpsw_btn();
							}
						}
					}
				})
			},
			//
			getCode() {
				var that = this;
				that.isshowCount = true;
				that.countDown();
				var sign = "jsjy" + that.mobile + "cgyc";
				sign = md5(sign);
				sign = sign.toUpperCase();
				var url = 'http://101.200.169.185/getTtSMSCode?mobile=' + that.mobile + '&sign=' + sign;
				that.$http.get(url).then(function(response){
					if(response.data.code=="200"){
						that.checkIfCode(response.data.smsCode);
					}
				}).catch(function(error){

				})
			},
			//
			checkIfCode(code) {
				var that = this;
				var url = 'code?filter={"where":{"mobile":'+ that.mobile +'}}';
				var method = "GET";
				that.ajax({url,method,
					success:function(response){
						if(response.length==0) {
							that.storeCode(code);
						}else {
							that.updateCode(code,response[0].id)
						}
					},
					error:function(data){}
				})
			},
			//
			storeCode(code) {
				var that = this;
				var url = "code";
				var method = "POST";
				var params = {
					"data":{
						"mobile":that.mobile,
						"status":"0",
						"code":code
					}
				}
				that.ajax({url,method,params,
					success:function(response){
					},
					error:function(data){}
				})
			},
			//code
			updateCode(code,id) {
				var that = this;
				var url = "code/" + id;
				var method = "POST";
				var params = {
					"data":{
						"code":code,
						"_method":"PUT"
					}
				}
				that.ajax({url,method,params,
					success:function(response){
					},
					error:function(data){}
				})
			},
			fgtpsw_btn() {
				if(!/^1[34578]\d{9}$/.test(this.mobile)) {
					this.$MessageBox.alert('');
					return
				}
				if(!this.code) {
					this.$MessageBox.alert('');
					return
				}
				if(!this.password) {
					this.$MessageBox.alert('');
					return
				}
				this.fgtpswAjax();
			},
			fgtpswAjax() {
				var that = this;
				that.$Indicator.open();
				//
				var url = 'code?filter={"where":{"mobile":'+ that.mobile +'}}';
				var method = "GET";
				that.ajax({url,method,
					success:function(response){
						if(response.length>0){
							var code = response[0].code;
							var time = new Date(response[0].updatedAt);
							time = time.getTime();
							var now = new Date().getTime();
							var shix = now-time;
							if(code==that.code&&shix<600000){
								//
								var url2 = "f_user/" + that.userId;
								var params = {
									"data": {
										"password": that.password,
										"_method":"PUT"
									}
								}
								that.ajax({url:url2,method:'POST',params,
									success:function(data){
										if(data){
											that.$Indicator.close();
											that.$MessageBox.alert("").then(function(){
												that.$router.backRoute();
											})
										}
									}
								})
							} else {
								that.$Indicator.close();
								that.$MessageBox.alert("");
							}
						} else {
							that.$Indicator.close();
							that.$MessageBox.alert("");
						}
					},
					error:function(data){}
				})
			},
		},
		activated() {
			this.userId = "";
			this.mobile = "";
			this.code = "";
			this.password = "";
		}
	}
</script>

<style scoped>
	.logo {
		width: 4rem;
		margin: 1.8rem auto 0;
	}
	.iptBox {
		margin:2.25rem auto 0;
		width:17.25rem;
	}
	.aui-btn-danger {
		background-color: #f22a2a !important;
		margin-top: 1.5rem;
	}
	.aui-list .aui-list-item-inner {
		margin-right: 0;
	}
</style>



