import mingyilist from './components/mine/mingyilist.vue'  //
import zhifu1 from './components/mine/zhifu1'  //
import shipin from './components/mine/shipin'  //
import jianhu from './components/mine/jianhu'  //
import jianhu_detail from './components/mine/jianhu_detail'  //
import fankui from './components/mine/fankui'//
import zhifu2  from './components/mine/zhifu2'  //
import newsdetail  from './components/mine/newsdetail'  //
import  imlist   from './components/mine/imlist'   //
import  im from './components/mine/im'   //
import  luntan from  './components/mine/luntan'   //
import  fatie from  './components/mine/fatie'   //
