<template>
	<div style="background-color: #fff; min-height: 100%;">
    <myHeader :title="''"></myHeader>

    <div>
      <div class="logo">
        <img src="static/image/logo.jpg" />
        <center><span style="color:#3F51B5;margin-top:10px;font-size:17px"></span></center>
      </div>
      <div class="iptBox aui-list aui-form-list">
        <div class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-label-icon">
              <img src="../../../static/image/mobileatByFoot.png" alt="" width="18" />
            </div>
            <div class="aui-list-item-input">
              <input type="tel" v-model="loginuser" placeholder="">
            </div>
          </div>
        </div>
        <div class="aui-list-item">
          <div class="aui-list-item-inner">
            <div class="aui-list-item-label-icon">
              <img src="../../../static/image/pswatByFoot.png" alt="" width="18" />
            </div>
            <div class="aui-list-item-input">
              <input type="password" v-model="password" placeholder="">
            </div>
          </div>
        </div>
        <div style="font-size:0.7rem;text-align:right;line-height:2.1rem;"><span @click="openForgetPsw"></span></div>
        <div class="aui-btn aui-btn-danger aui-btn-block" @click="btn_login"> </div>
        <div class="aui-btn aui-btn-block aui-btn-outlined" style="margin-top:15px;color:#a2a8ae;border-color:#a2a8ae;" @click="openRegister"> </div>
      </div>
    </div>
  </div>
</template>

<script>
	export default {
		name: 'login',
		data() {
      return {
        loginuser : '',
        password : ''
      }
    },
    methods: {
      openRegister(){
        this.$router.pushRoute({name:'register'});
      },
      openForgetPsw(){
        this.$router.pushRoute({name:'forgetPsw'});
      },
      btn_login:function() {
        if(!/^1[34578]\d{9}$/.test(this.loginuser)) {
          this.$MessageBox.alert('');
          return
        }
        if(!this.password) {
          this.$MessageBox.alert('');
          return
        }
        this.loginAjax();
      },
      loginAjax:function() {
        var that = this;

				//
				var url1 = 'f_user/count?filter={"where":{"User_name":' + that.loginuser + '}}';
				var method = 'get';
				that.ajax({url:url1,method,
					success:function(data) {
            //
            if (data.count!==0) {
              var url2 = "f_user?filter[where][User_name]=" + that.loginuser;
              url2 += '&filter[where][password]=' + that.password;
              url2 += '&filter[where][info]=1';
              url2 += '&filter[fields][password]=false'
              that.ajax({url:url2, method,
               success:function(data) {
                if (JSON.stringify(data) == '[]') {
                  that.$MessageBox.alert('');
                } else {
                  window.localStorage.setItem('userMobile',data[0].User_name);
                  window.localStorage.setItem('userId',data[0].id);
                  that.$router.backRoute();
                }
              }
            });
						} else { //
							that.$MessageBox.alert('');
						}
					}
				})
			}
		},
		activated() {
			this.loginuser = window.localStorage.getItem('userMobile');
			this.password = '';
			if (window.localStorage.getItem('userId')) {
				this.$router.backRoute();
			}
		}
	}
</script>

<style scoped>
  .logo {
    width: 4rem;
    margin: 1.8rem auto 0;
  }
  .iptBox {
    margin:1.5rem auto 0;
    width:17.25rem;
  }
  .aui-btn-danger {
    background-color: #28B8A1 !important;
    margin-top: 0.6rem;
  }
</style>



